import * as React from 'react';
import styled from 'styled-components';
import { Title, TextBody, TextStar } from 'ui/atoms';
import ovalImg from 'assets/images/svg/oval.svg';
import { device } from 'lib/constants';

interface ListItem {
  body: string;
  number: number;
  title: string;
}

export const List: React.FC<{
  items: ListItem[];
}> = ({ items }) => (
  <>
    {items.map(({ title, body, number }) => (
      <Item key={number}>
        <Oval>
          <TextStar data-size="m">{number}</TextStar>
        </Oval>
        <Text>
          <TitleBlock>
            <Title size="s">{title}</Title>
          </TitleBlock>
          <TextBodyBlock>
            <TextBody color="secondary">{body}</TextBody>
          </TextBodyBlock>
        </Text>
      </Item>
    ))}
  </>
);

export const Text = styled.div`
  text-align: left;
  width: 220px;
  font-size: 12px;
  font-weight: 500;
  padding-left: 18px;
  padding-bottom: 30px;

  @media ${device.tablet} {
    padding-left: 37px;
    width: 476px;
  }
  @media ${device.desktop} {
    width: 700px;
  }
`;

export const Item = styled.div`
  display: flex;
  :last-child {
    ${Text} {
      padding-bottom: 24px;
      @media ${device.desktop} {
        padding-bottom: 117px;
      }
    }
  }
`;

export const TitleBlock = styled.div`
  padding-bottom: 9px;
  ${Title} {
    text-align: left;
  }

  @media ${device.tablet} {
    padding-bottom: 13px;
    ${Title} {
      line-height: 1.29;
    }
  }
  @media ${device.desktop} {
    padding-bottom: 12px;
  }
`;

export const Oval = styled.div`
  background-image: url(${ovalImg});
  background-size: 100% 100%;
  width: 33px;
  height: 33px;
  object-fit: contain;
  margin-top: 2px;
`;

export const TextBodyBlock = styled.div`
  div {
    line-height: 1.5;
    text-align: left;
  }
  @media ${device.tablet} {
    width: 465px;
  }
  @media ${device.desktop} {
    width: 647px;
  }
`;
