import * as React from 'react';
import styled from 'styled-components';
import { device, tabletSize } from 'lib/constants';
import { ButtonSubscribe } from 'ui/molecules';
import { Sidebar } from './sidebar';
import { MenuItems } from './menu-items';
import { formEvent } from 'lib/events';
import { useWindowWidth } from 'lib/hooks';

interface MenuProps {
  items: { text: string; link: string }[];
  buttonMenuText: string;
  licence: string;
}

export const Menu: React.FC<MenuProps> = ({
  items,
  buttonMenuText,
  licence,
}) => {
  const [isVisible, setVisible] = React.useState(false);
  const [menuView, setMenuView] = React.useState(false);
  const width = useWindowWidth();

  React.useEffect(() => {
    window.addEventListener('scroll', handleScroll);
    return () => window.removeEventListener('scroll', handleScroll);
  });

  const handleScroll = React.useCallback(() => {
    const { scrollY } = window;
    if (scrollY <= 0) {
      setMenuView(false);
    } else {
      setMenuView(true);
    }
  }, []);

  const toggler = React.useCallback(() => {
    setVisible(!isVisible);
  }, [isVisible]);

  if (width < tabletSize)
    return (
      <TopBlock data-scroll={menuView}>
        <Sidebar
          isVisible={isVisible}
          toggler={toggler}
          menu={items}
          licence={licence}
        />
      </TopBlock>
    );

  return (
    <TopBlock data-scroll={menuView}>
      <Nav>
        <MenuItems items={items} className="menu" />
        <ButtonMenu onClick={formEvent}>
          <ButtonSubscribe link="/subscribe">{buttonMenuText}</ButtonSubscribe>
        </ButtonMenu>
      </Nav>
    </TopBlock>
  );
};

export const Nav = styled.nav`
  padding: 24px 0;
  display: none;
  width: 100%;
  align-items: center;
  justify-content: space-between;
  height: 48px;

  @media ${device.tablet} {
    margin: 0 auto;
    display: flex;
    max-width: 768px;
  }

  @media ${device.desktop} {
    max-width: 1440px;
  }
`;

export const ButtonMenu = styled.div`
  display: none;
  padding-right: 64px;

  button {
    height: 48px;
    font-size: 16px;
    width: 170px;
    padding: 0 10px;
  }
`;

export const TopBlock = styled.div`
  position: absolute;
  z-index: 2;
  width: 100%;

  @media ${device.tablet} {
    width: 100%;
    display: flex;
    justify-content: flex-start;
    position: fixed;

    &[data-scroll='true'] {
      background: #2a2a4f;
      box-shadow: 0 2px 14px 0 rgba(0, 0, 0, 0.04),
        inset 0 -1px 0 0 rgba(0, 0, 0, 0.05);
      max-height: 95px;
      transition: 0.3s;
      background: #2a2a4f;
      @media ${device.tablet} {
        ${ButtonMenu} {
          display: flex;
        }
      }
    }
  }
`;
