import * as React from 'react';
import styled from 'styled-components';
import appStoreImg from 'assets/images/svg/app-store-button.svg';
import googlePlayImg from 'assets/images/svg/google-play-button.svg';
import { device } from 'lib/constants';
import ScrollIntoView from 'react-scroll-into-view';

interface ButtonsDownloadProps {
  event(platform: 'ios' | 'android', screen: string): void;
  screen: string;
}

export const ButtonsDownload: React.FC<ButtonsDownloadProps> = ({
  event,
  screen,
}) => {
  const onClickIos = React.useCallback(() => event('ios', screen), [
    screen,
    event,
  ]);
  const onClickAndroid = React.useCallback(() => event('android', screen), [
    screen,
    event,
  ]);
  return (
    <Buttons>
      <Button onClick={onClickIos}>
        <ScrollIntoView selector="#subscribe">
          <AppStoreButton />
        </ScrollIntoView>
      </Button>
      <Button onClick={onClickAndroid}>
        <ScrollIntoView selector="#subscribe">
          <GooglePlayButton />
        </ScrollIntoView>
      </Button>
    </Buttons>
  );
};

export const AppStoreButton = styled.div`
  margin: 0 5px;
  background-image: url(${appStoreImg});
  width: 211px;
  height: 69px;
  background-size: cover;
  &:hover {
    cursor: pointer;
  }
  @media ${device.tablet} {
    width: 294px;
    height: 89px;
  }
`;

export const GooglePlayButton = styled.div`
  padding: 0 5px;
  background-image: url(${googlePlayImg});
  width: 211px;
  height: 69px;
  background-size: cover;
  &:hover {
    cursor: pointer;
  }
  @media ${device.tablet} {
    width: 294px;
    height: 89px;
  }
`;

export const Button = styled.div`
  display: inline-block;
`;

export const Buttons = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  @media ${device.tablet} {
    flex-direction: row;
  }
`;
