import * as React from 'react';
import styled from 'styled-components';
import { BrowserRouter as Router, Switch, Route } from 'react-router-dom';

import { StarMath, Games, Method, Goal, Subscribe } from './pages';
import { device } from 'lib/constants';
import { contentGet, Pages } from 'content';
import { Menu } from 'ui/molecules';
import { clouds } from 'assets/images/svg';
import { ContentPagesProps } from 'content/content-get';
import { menuCreate } from './lib/menu';

export const App: React.FC = () => {
  const personalApproach = contentGet(Pages.PersonalApproach);
  const kidTendToForget = contentGet(Pages.KidTendToForget);
  const schoolApproach = contentGet(Pages.SchoolApproach);
  const understantNotLearn = contentGet(Pages.UnderstantNotLearn);
  return (
    <Router>
      <Switch>
        <Route exact path="/">
          <StarMathPage content={understantNotLearn} path="/" />
        </Route>
        <Route exact path="/kid-tend-to-forget">
          <StarMathPage content={kidTendToForget} path="/kid-tend-to-forget" />
        </Route>
        <Route exact path="/school-approach">
          <StarMathPage content={schoolApproach} path="/school-approach" />
        </Route>
        <Route exact path="/personal-approach">
          <StarMathPage content={personalApproach} path="/personal-approach" />
        </Route>
        <Route exact path="/subscribe">
          <SubscribePage content={understantNotLearn} />
        </Route>
      </Switch>
    </Router>
  );
};

export const StarMathPage: React.FC<{
  content: ContentPagesProps;
  path: string;
}> = ({ content, path }) => {
  const { navigation, starMath, game, method, goal } = content;
  const menu = menuCreate(navigation, path);

  return (
    <Container>
      <Cloud>
        <CloudImg src={clouds} />
      </Cloud>
      <Menu
        items={menu}
        buttonMenuText={navigation.buttonMenuText}
        licence={navigation.sidemenuLicence}
      />
      <StarMath content={starMath} />
      <Year>2020</Year>
      <Games content={game} />
      <Method content={method} />
      <Goal content={goal} />
    </Container>
  );
};

export const SubscribePage: React.FC<{ content: ContentPagesProps }> = ({
  content,
}) => {
  const { subscribe, navigation } = content;
  const menu = menuCreate(navigation, '/');

  return (
    <Container>
      <Menu
        items={menu}
        buttonMenuText={navigation.buttonMenuText}
        licence={navigation.sidemenuLicence}
      />
      <Subscribe content={subscribe} />
    </Container>
  );
};

export const Container = styled.div`
  overflow: hidden;
  position: relative;
`;

export const Year = styled.div`
  font-size: 12px;
  font-weight: 600;
  z-index: 1;
  color: #ededed;
  position: absolute;
  transform: rotate(-90deg);
  opacity: 0.2;
  left: 15px;
  @media ${device.tablet} {
    display: none;
  }
`;

export const CloudImg = styled.img`
  height: auto;
  width: 274px;

  @media ${device.tablet} {
    width: 385px;
  }
  @media ${device.desktop} {
    width: 502px;
  }
`;

export const Cloud = styled.div`
  position: absolute;
  top: 0;
  right: -50px;
  z-index: 1;
  @media ${device.tablet} {
    right: -80px;
  }

  @media ${device.desktop} {
    right: 0;
  }
`;
