import * as React from 'react';
import styled from 'styled-components';

import { device } from 'lib/constants';
import { Link, useLocation } from 'react-router-dom';

interface PositionsProps {
  from: number;
  to: number;
  link: string;
}

interface MenuItemsProps {
  className: string;
  items: { text: string; link: string }[];
  onMenuClick?(event?: React.SyntheticEvent): void;
}

export const MenuItems: React.FC<MenuItemsProps> = ({
  items,
  className,
  onMenuClick,
}) => {
  const [activeMenu, setActiveMenu] = React.useState();
  const { pathname, hash } = useLocation();
  const [pages, setPages] = React.useState<PositionsProps[]>([]);

  React.useEffect(() => {
    window.addEventListener('scroll', handleScroll);
    return () => window.removeEventListener('scroll', handleScroll);
  });

  React.useEffect(() => {
    const allPages = document.getElementsByClassName('page');
    const pages = [...allPages].reduce(
      (pagesArray: PositionsProps[], page: any) => {
        pagesArray.push({
          from: page.offsetTop,
          to: page.offsetTop + page.offsetHeight,
          link: pathname + '#' + page.dataset.id,
        });
        return pagesArray;
      },
      [],
    );
    setPages(pages);
  }, [pathname]);

  const getMenuItem = React.useCallback(
    (position: number) => {
      const page = pages.find((el) => el.from < position && position < el.to);
      return page;
    },
    [pages],
  );

  const handleScroll = React.useCallback(() => {
    const { scrollY, innerHeight } = window;
    const page = getMenuItem(scrollY + (innerHeight / 3) * 2);
    if (page) {
      setActiveMenu(page.link);
    }
  }, [getMenuItem]);

  React.useEffect(() => {
    setActiveMenu(pathname + hash);
    if (pathname !== '/subscribe' && !hash) {
      setActiveMenu(items[0].link);
    }
    if (!hash) {
      window.scrollTo(0, 0);
    }

    if (hash) {
      document
        .getElementById(hash.slice(1))
        ?.scrollIntoView({ behavior: 'smooth' });
    }
  }, [pathname, hash, items]);

  return (
    <MenuBlock className={className}>
      {items.map(({ text, link }, key) => (
        <MenuItem
          key={key}
          onClick={onMenuClick}
          className={activeMenu === link ? 'active' : ''}
        >
          <MenuLink to={link}>{text}</MenuLink>
        </MenuItem>
      ))}
    </MenuBlock>
  );
};

export const MenuBlock = styled.ul`
  padding-left: 15px;
  margin: 0;

  @media ${device.tablet} {
    display: flex;
  }
  @media ${device.desktop} {
    padding-left: 135px;
  }
`;

export const MenuItem = styled.li`
  list-style-type: none;
  padding-right: 45px;
  &:hover {
    cursor: pointer;
    a {
      text-shadow: 0 0 12px rgba(255, 248, 221, 0.55);
      background-image: radial-gradient(
        circle at 44% 50%,
        #ffbf5d,
        #ff9a00 90%
      );
      font-size: 12px;
      font-weight: 800;
      font-stretch: normal;
      font-style: normal;
      line-height: 1.5;
      letter-spacing: normal;
      -webkit-background-clip: text;
      background-clip: text;
      -webkit-text-fill-color: transparent;
      opacity: 1;
    }
  }
  &.active {
    a {
      text-shadow: 0 0 12px rgba(255, 248, 221, 0.55);
      background-image: radial-gradient(
        circle at 44% 50%,
        #ffbf5d,
        #ff9a00 90%
      );
      font-size: 12px;
      font-weight: 800;
      font-stretch: normal;
      font-style: normal;
      line-height: 1.5;
      letter-spacing: normal;
      -webkit-background-clip: text;
      background-clip: text;
      -webkit-text-fill-color: transparent;
      opacity: 1;
    }
  }
`;

export const MenuLink = styled(Link)`
  opacity: 0.6;
  color: var(--primary);
  font-size: 12px;
  font-weight: 800;
  line-height: 1.75;
  text-decoration: none;
  padding: 24px 0;
`;
