export const starMathEvent = () => {
  if (process.env.REACT_APP_STAGE === 'production') {
    window.gtag('event', 'click', {
      event_category: 'button',
      event_action: 'first_screen',
    });
  }
};

export const appDownloadEvent = (
  platform: 'ios' | 'android',
  screen: string,
) => {
  if (process.env.REACT_APP_STAGE === 'production') {
    window.gtag('event', 'click', {
      event_category: 'button',
      event_action: `${screen}_${platform}`,
    });
  }
};

