import * as React from 'react';
import {
  ButtonDescription,
  Description,
  Feedback,
  Header,
  License,
  Line,
  SubscribeContent,
  SubscribePage,
} from './style';
import { Title, TextBody } from 'ui/atoms';
import { FormFeedback } from 'ui/molecules';
import { Layout } from 'ui/templates';
import { bgSubscribe } from 'assets/images/background';
import MailchimpSubscribe from 'react-mailchimp-subscribe';
import { url } from 'lib/constants';

export const Subscribe: React.FC<{ content: any }> = ({ content }) => {
  return (
    <SubscribePage className="page" data-id="subscribe">
      <Layout
        background={bgSubscribe}
        smallBackground={bgSubscribe}
        id="subscribe"
      >
        <SubscribeContent>
          <Header>
            <Title size="l">{content.subscribeTitle}</Title>
          </Header>
          <Description>
            <TextBody color="basic">{content.subscribeText}</TextBody>
          </Description>
          <Feedback>
            <MailchimpSubscribe
              url={url}
              render={({ subscribe, status, message }) => (
                <FormFeedback
                  status={status}
                  message={message}
                  onValidated={(formData) => subscribe(formData)}
                />
              )}
            />
          </Feedback>
          <ButtonDescription>
            <TextBody color="secondary">{content.subscribeBottomText}</TextBody>
          </ButtonDescription>
          <Line />
          <License>
            <TextBody color="secondary" size="s">
              {content.subscribeLicense}
            </TextBody>
          </License>
        </SubscribeContent>
      </Layout>
    </SubscribePage>
  );
};
