type Navigation = {
  menuStarMath: string;
  menuGames: string;
  menuOurMethod: string;
  menuOurGoal: string;
  menuSubscribe: string;
};

export const menuCreate = (navigation: Navigation, page: string) => [
  { text: navigation.menuStarMath, link: page + '#main' },
  { text: navigation.menuGames, link: page + '#games' },
  { text: navigation.menuOurMethod, link: page + '#method' },
  { text: navigation.menuOurGoal, link: page + '#goal' },
  { text: navigation.menuSubscribe, link: '/subscribe' },
];
