import * as React from 'react';
import {
  Anchor,
  Asteroid,
  ButtonBlock,
  ButtonDescription,
  Description,
  Header,
  ListBlock,
  MethodPage,
  Shape,
} from './style';
import { Title, TextBody } from 'ui/atoms';
import { List, ButtonSubscribe } from 'ui/molecules';
import { Layout } from 'ui/templates';
import { bgMethod, bgTabletMethod } from 'assets/images/background';
import shape from 'assets/images/svg/shape3.svg';
import asteroid from 'assets/images/png/asteroid.png';
import { methodEvent } from 'lib/events';

export const Method: React.FC<{ content: any }> = ({ content }) => {
  const listItems = [
    {
      title: content.methodListTitle1,
      number: 1,
      body: content.methodListText1,
    },
    {
      title: content.methodListTitle2,
      number: 2,
      body: content.methodListText2,
    },
    {
      title: content.methodListTitle3,
      number: 3,
      body: content.methodListText3,
    },
  ];
  return (
    <MethodPage className="page" data-id="method">
      <Anchor id="method" />
      <Layout background={bgMethod} smallBackground={bgTabletMethod}>
        <Header>
          <Title size="m">{content.methodTitle}</Title>
        </Header>
        <Description>
          <TextBody color="basic">{content.methodSubtitle}</TextBody>
        </Description>
        <ListBlock>
          <List items={listItems} />
        </ListBlock>
        <ButtonBlock onClick={methodEvent}>
          <ButtonSubscribe link="/subscribe">
            {content.buttonText}
          </ButtonSubscribe>
        </ButtonBlock>
        <ButtonDescription>
          <Title size="xs">{content.methodBottomBlockTitle}</Title>
          <TextBody size="s" color="secondary">
            {content.methodBottomBlockText}
          </TextBody>
        </ButtonDescription>
        <Asteroid src={asteroid} />
      </Layout>
      <Shape src={shape} />
    </MethodPage>
  );
};
