import { pagesContentSet } from 'lib/content-set';

import understantNotLearn from './understant-not-learn.json';
import kidTendToForget from './kid-tend-to-forget.json';
import schoolApproach from './school-approach.json';
import personalApproach from './personal-approach.json';

export enum Pages {
  UnderstantNotLearn,
  KidTendToForget,
  SchoolApproach,
  PersonalApproach,
}

export type ContentPagesProps = {
  starMath: any;
  game: any;
  method: any;
  goal: any;
  subscribe: any;
  navigation: any;
};

export const contentGet: (link: Pages) => ContentPagesProps = (link) => {
  if (link === Pages.KidTendToForget) {
    return pagesContentSet(kidTendToForget);
  }
  if (link === Pages.SchoolApproach) {
    return pagesContentSet(schoolApproach);
  }
  if (link === Pages.PersonalApproach) {
    return pagesContentSet(personalApproach);
  }
  return pagesContentSet(understantNotLearn);
};
