import * as React from 'react';
import styled from 'styled-components';
import { device } from 'lib/constants';

interface LayoutProps {
  background: string;
  id?: string;
  smallBackground: string;
}

interface BackgroundProps {
  background: string;
  smallBackground: string;
}

export const Layout: React.FC<LayoutProps> = ({
  background,
  id,
  smallBackground,
  children,
}) => (
  <Background background={background} smallBackground={smallBackground} id={id}>
    <Container>
      <Content>{children}</Content>
    </Container>
  </Background>
);

export const Background = styled.div<BackgroundProps>`
  background: #302e57 no-repeat center;
  overflow: hidden;
  position: relative;
  display: flex;
  justify-content: center;
  background-image: url(${({ smallBackground }) => smallBackground});
  background-size: cover;
  @media ${device.desktop} {
    background-image: url(${({ background }) => background});
  }
`;

export const Content = styled.div`
  width: 290px;
  display: flex;
  flex-direction: column;

  @media ${device.tablet} {
    width: 597px;
  }
  @media ${device.desktop} {
    width: 985px;
  }
`;

export const Container = styled.div`
  max-width: 320px;
  width: 100%;
  display: flex;
  justify-content: center;
  z-index: 1;

  @media ${device.tablet} {
    max-width: 768px;
  }

  @media ${device.desktop} {
    max-width: 1440px;
  }
`;
